  startTime: ({
    commit,
    dispatch
  }) => {
    StopTime(timeToLoginAlert)
    StopTime(timeToLogout)

    timeToLogout = setTimeout(() => {
      dispatch('logout')
      commit('loginAlert', false)
      router.push('/login')
    }, 1000 * 60 * 59)
    timeToLoginAlert = setTimeout(() => {
      commit('loginAlert', true)
    }, 1000 * 60 * 58)
  },
  relogin({
    commit,
    state,
    dispatch
  }) {
    const reLoginQuery = {
      query: `{
        relogin{
          userId
          token
          tokenExpiration
          name
          role
        }
      }`
    }
    const headers = {
      headers: {
        'Content-Type': 'application/json',
        Authorization: 'Bearer ' + state.auth.token
      }
    }
    return new Promise((resolve, reject) => {


      axios
        .post(process.env.VUE_APP_API_ENDPOINT, reLoginQuery, headers)
        .then(res => {

          if (res.status !== 200 && res.status !== 201) {
            throw new Error('relogin Failed!')

          }


          if (res.data.errors && res.data.errors.length > 0) {
            dispatch('logout')
            router.push('/login')
            return
          }


          const auth = res.data.data.relogin

          localStorage.setItem('token', auth.token)
          localStorage.setItem('userId', auth.userId)
          localStorage.setItem('tokenExpiration', auth.tokenExpiration)
          localStorage.setItem('name', auth.name)
          commit('setAuth', auth)

          commit('loginAlert', false)

          return res
        }).then(res => {
          dispatch('loadData')
          dispatch('startTime')
          dispatch('subscribeWebPush')
          resolve(res)
        })
        .catch(err => {
          console.log(err)
          reject(err)
        })

    })
  },

