<template>
  <material-card
    color="accent"
    title="Próximos Eventos"
    text="Hasta la fecha"
  >
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-calendar-search"
        label="Buscar"
        single-line
        hide-details
      />
    </v-card-title>

    <v-list
      two-line
      class="upcoming-events-list"
    >
      <template v-for="(item, index) in UpcomingSortedEvents">
        <v-list-tile
          :key="item.title"
          ripple
          :id="index"
          :class="{swipeRight: swipeRight === index, swipeLeft: swipeLeft === index}"
          v-touch="{
            right: () => showBtn(index),
            left: () => hideBtn(index)
          }"
          @click="goToEvent(item._id)"
        >
          <div
            class="delBtn"
            @click="alertDeleteEvent(item._id, index)"
          >
            <v-btn
              flat
              icon
            >
              <v-icon>mdi-delete</v-icon>
              <span>Eliminar</span>
            </v-btn>
          </div>
          <!-- @click="goToEvent(item._id)" -->
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            <v-list-tile-sub-title class="text--primary">{{ item.eventDate }}</v-list-tile-sub-title>
            <v-list-tile-sub-title v-if="item.cordinator">Coordinador: {{item.cordinator}}</v-list-tile-sub-title>
            <v-list-tile-sub-title v-else>Creado por: {{item.createdBy.name }}</v-list-tile-sub-title>
          </v-list-tile-content>
          <div
            class="actionBtn"
            @click="goToAdminEvent(item._id)"
          >
            <v-btn
              flat
              icon
            >
              <v-icon>mdi-calendar</v-icon>
              <span>Admin</span>
            </v-btn>
          </div>

          <v-list-tile-action>
            <v-btn
              icon
              ripple
              @click="show = index"
            >
              <v-icon
                small
                :color="item.color"
              >mdi-circle</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
        <v-divider
          v-if="index + 1 < items.length"
          :key="index"
        ></v-divider>
      </template>
    </v-list>

    <v-dialog
      v-model="showAlertDeleteEvent"
      persistent
      max-width="390"
    >
      <v-card>
        <v-card-title
          dark
          class="headline error white--text"
        >¿Desea eliminar este evento?</v-card-title>
        <v-card-text>Presione Aceptar para eliminar definitivamente este evento.</v-card-text>
        <v-card-actions>
          <v-spacer/>
          <v-btn
            color="info"
            @click="resetDeleteEvent"
          >Cancelar</v-btn>
          <v-btn
            :disabled="isLoading"
            :loading="isLoading"
            color="error"
            @click="deleteEvent"
          >Aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </material-card>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'EventsList',
  data: () => ({
    actionsBtn: false,
    delBtn: false,
    index: null,
    swipeLeft: null,
    swipeRight: null,
    months: [
      'Enero',
      'Febrero',
      'Marzo',
      'Abril',
      'Mayo',
      'Junio',
      'Julio',
      'Agosto',
      'Septiembre',
      'Octubre',
      'Noviembre',
      'Diciembre'
    ],
    deleteEventId: null,
    showAlertDeleteEvent: false,
    search: '',
    headers: [
      {
        sortable: true,
        text: 'Fecha y Hora',
        value: 'eventDate'
      },
      {
        sortable: true,
        text: 'Titulo',
        value: 'title'
      },
      {
        sortable: false,
        text: 'Creador',
        value: 'createdBy.name'
      },
      {
        sortable: false,
        text: 'Accción',
        value: 'name',
        class: 'text-xs-center'
      }
    ]
    // items: []
  }),
  computed: {
    items() {
      const items = this.$store.state.events

      function pad(n) {
        return n < 10 ? '0' + n : n
      }
      return items.map(item => {
        return {
          ...item,
          eventDate: new Date(item.eventDate).toLocaleTimeString(['es-VE'], {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric',
            hour: '2-digit',
            minute: '2-digit'
          }),
          month: this.months[new Date(item.eventDate).getMonth()],
          year: new Date(item.eventDate).getFullYear(),
          day: new Date(item.eventDate).getDate(),
          color: this.getColor(item.orgScope)
        }
      })
    },
    UpcomingSortedEvents() {
      return this.sort(this.items)
    },
    ...mapState(['isLoading'])
  },

  methods: {
    move(e) {},
    showBtn(index) {
      if (this.actionsBtn) {
        this.swipeLeft = null
        this.actionsBtn = false
      } else {
        this.index = index
        this.swipeRight = index
        this.delBtn = true
      }
    },
    showActions(index) {
      this.index = index
      this.swipeLeft = index
      this.actionsBtn = true
    },
    hideBtn(index) {
      if (this.delBtn) {
        this.swipeRight = null
        this.index = null
        this.delBtn = false
      } else {
        this.showActions(index)
      }
    },
    getColor(orgScope) {
      const matchArea = this.$store.getters.getAreaScope.filter(area => {
        return area.title === orgScope
      })[0]

      if (matchArea) {
        return matchArea.color
      } else {
        return '#c4c4c4'
      }
    },
    sort(items) {
      const vue = this
      const itemsSorted = items.sort(function(a, b) {
        a = new Date(a.year, vue.months.indexOf(a.month) + 1, a.day)
        b = new Date(b.year, vue.months.indexOf(b.month) + 1, b.day)
        return b > a ? -1 : b < a ? 1 : 0
      })

      const UpcomingSortedEvents = itemsSorted.filter(item => {
        const today = new Date().getTime()
        const itemDateFormated = {
          ...item,
          evenDate: new Date(
            item.year,
            vue.months.indexOf(item.month) + 1,
            item.day
          ).getTime()
        }

        const itemToreturn = itemDateFormated.evenDate === today

        return itemDateFormated.evenDate >= today
      })

      return UpcomingSortedEvents
    },
    resetDeleteEvent() {
      this.deleteEventId = null
      this.showAlertDeleteEvent = false
      this.hideBtn(this.index)
    },
    goToEvent(id) {
      this.$router.push('/events/event/' + id)
    },
    goToAdminEvent(id) {
      this.$router.push('/events/event/' + id + '/admin')
    },
    alertDeleteEvent(id) {
      this.deleteEventId = id
      this.showAlertDeleteEvent = true
    },

    deleteEvent() {
      this.$store.dispatch('setLoading', true)
      this.$store
        .dispatch('deleteEvent', this.deleteEventId)
        .then(async res => {
          await this.$store.dispatch('fetchEvents')
          return res
        })
        .then(res => {
          console.log(res.data)
          const deletedEvent = res.data.data.deleteEvent

          if (res.status === 200) {
            this.$store.dispatch('toggleAlert', {
              active: true,
              class: 'error',
              msg: `El evento ${deletedEvent.title} fue borrado exitosamente`
            })
          } else {
            this.$store.dispatch('showAlert', {
              class: 'warning darken-2',
              active: true,
              msg: 'Algo no anda bien aqui :('
            })
          }
          this.$store.dispatch('setLoading', false)
          this.showAlertDeleteEvent = false
        })
    }
  }
}
</script>
<style lang="scss" scope>
.action-btn.v-btn {
  margin-left: 5px;
}

.v-icon.mdi.mdi-chevron-right.theme--light,
.v-icon.mdi.mdi-chevron-left.theme--light {
  color: rgba(0, 0, 0, 0.54);
}

.upcoming-events-list {
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
}
.delBtn {
  background-color: #f55a4e;
  position: absolute;
  width: 100px;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(-100px);
  color: #fff;
  transition: all 300ms ease-in-out;
}
.actionBtn {
  background-color: #82b1ff;
  position: absolute;
  width: 100px;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100px);
  color: #fff;
  transition: all 300ms ease-in-out;
}

.swipeRight {
  transform: translateX(100px);
}
.swipeLeft {
  transform: translateX(-100px);
}
</style>
