<template>
  <v-app>
    <core-filter/>

    <core-toolbar/>

    <core-drawer/>

    <core-view/>
  </v-app>
</template>
<script>
import gql from 'graphql-tag'
import { rejects } from 'assert'
export default {
  apollo: {
    // Subscriptions
    $subscribe: {
      // When a tag is added
      newEvent: {
        query: gql`
          subscription {
            newEvent {
              _id
              active
              cordinator
              guests {
                _id
                name
              }
              memberAsist {
                name
                _id
              }
              createdBy {
                _id
                name
              }
              description
              endDate
              eventDate
              orgScope
              place
              staffAuv
              staffDarte
              staffEci
              staffMav
              staffProto
              staffVar
              title
            }
          }
        `,
        // Reactive variables
        variables() {
          // This works just like regular queries
          // and will re-subscribe with the right variables
          // each time the values change
        },
        // Result hook
        result(data) {
          console.log(data)
          this.$store.commit('addEvent', data.data.newEvent)
        }
      },
      userUpdated: {
        query: gql`
          subscription {
            userUpdated {
              _id
              email
              name
              role
              subs {
                _id
              }
            }
          }
        `,
        variables() {
          // This works just like regular queries
          // and will re-subscribe with the right variables
          // each time the values change
        },
        // Result hook
        result(data) {
          console.log(data)
          this.$store.commit('updateUser', data.data.userUpdated)
        }
      }
    }
  },
  methods: {
    registerSW() {
      return new Promise((resolve, reject) => {
        const vue = this
        if ('serviceWorker' in navigator) {
          console.log('ServiceWorker in navigator')
          window.addEventListener('load', function() {
            console.log('Window loaded...')
            navigator.serviceWorker.register('/service-worker.js').then(
              function(registration) {
                // Registration was successful
                console.log(
                  'ServiceWorker registration successful with scope: ',
                  registration.scope
                )
                console.log(registration)

                vue.$store.commit('setSW_registration', registration)
                resolve(registration)
              },
              function(err) {
                // registration failed :(
                console.log('ServiceWorker registration failed: ', err)
                rejects(err)
              }
            )
          })
        }
      })
    }
  },
  mounted() {
    this.registerSW()
      .then(registration => {
        if (this.$store.state.auth.token) {
          this.$store.dispatch('relogin').then(res => {})
        }
      })
      .catch(err => console.log(err))
  }
}
</script>

